<template>
  <div of="tpl-rt" class="area2 pirt" :class="`txt-${unisize}`">
    <label>
      <slot>PInput Sample Text</slot>
      <input v-model="value" :type="computedType" :class="`in-${unisize}`" style="width:3em;" />
    </label>
    <slot name="after"></slot>
  </div>
</template>
<script>
export default {
  props: {
    unisize: { type: String },
    type: { type: String },
    modelValue: { required: true },
  },
  computed: {
    computedType: {
      get() {
        switch (this.type) {
          case "toggle":
            return "checkbox";
          default:
            return this.type;
        }
      },
    },
    value: {
      get() {
        return this.modelValue;
      },
      set(value) {
        this.$emit("update:modelValue", value);
      },
    },
  },
};
</script>

<style lang="stylus" scoped>
.pirt {
  // p input root
  width: max-content;
  padding-top: 0;
  padding-bottom: 0;
}
</style>
