// Initialize the Amazon Cognito credentials provider
// import AWS from "https://sdk.amazonaws.com/js/aws-sdk-2.410.0.min.js" // this is only for local file
// require("https://sdk.amazonaws.com/js/aws-sdk-2.410.0.min.js");
var AWS = require("aws-sdk/dist/aws-sdk-react-native");
// module.export = { initialize, fetchUrl, fetchBlob } // this will cause when import, needed import VAR as fetchUrl from...
export default { initialize, fetchUrl, fetchBlob, fetchU16Str };

var polly;
var signer;
var initialized = false;

var speechParams = {
  OutputFormat: "mp3",
  SampleRate: "16000",
  Text: "Dear Pablo, this is the original sample text in your Polly.js File.",
  TextType: "text",
  VoiceId: "Matthew",
};

function initialize() {
  AWS.config.region = "eu-west-2"; // Region
  AWS.config.credentials = new AWS.CognitoIdentityCredentials({
    IdentityPoolId: "eu-west-2:708930b0-4a99-42b5-94ed-2b8c6f58ea44",
  });
  polly = new AWS.Polly({ apiVersion: "2016-06-10" });
  signer = new AWS.Polly.Presigner(speechParams, polly);

  // done initializing
  initialized = true;
  console.log("[Polly] polly initialized successfully.");
}

async function fetchUrl(text) {
  if (!initialized) await initialize();
  speechParams.Text = text;
  return new Promise((resolve, reject) => {
    signer.getSynthesizeSpeechUrl(speechParams, function (error, url) {
      if (error) {
        reject(error);
      } else {
        resolve(url);
      }
      return;
    });
  });
}

async function fetchBlob(text) {
  if (!initialized) await initialize();
  speechParams.Text = text;

  return new Promise((resolve, reject) => {
    polly.synthesizeSpeech(speechParams, function (error, data) {
      if (error) {
        reject(error);
      } else {
        let u8A = new Uint8Array(data.AudioStream);
        let voiceBlob = new Blob([u8A.buffer]);
        // let url = URL.createObjectURL(voiceBlob)
        resolve(voiceBlob);
      }
      return;
    });
  });
}

async function fetchU16Str(text) {
  if (!initialized) await initialize();
  speechParams.Text = text;

  return new Promise((resolve, reject) => {
    polly.synthesizeSpeech(speechParams, function (error, data) {
      if (error) {
        reject(error);
      } else {
        let u16A = new Uint16Array(data.AudioStream);
        let u16Str = String.fromCharCode.apply(null, u16A);
        resolve(u16Str);
      }
      return;
    });
  });
}
