<template>
  <div
    style="position:fixed; border:none; top:0; left:0; width:100%; display:flex; justify-content:center; align-items:center; pointer-events: none;"
    unselectable="on"
    onselectstart="return false;"
    ondragstart="return false;"
  >
    <div
      style="border:yellow solid 10px; background-color:rgba(0,0,0,0.8); margin-top:10%;
      pointer-events:auto;"
    >
      <div style="font-size:48px;">ADMIN WINDOW</div>password:
      <input v-model="pswd" />
      <br />
      <button @click="downloadLS">download localstorage words</button>
      <button @click="$emit('close-admin-window')">close</button>
    </div>
  </div>
</template>
<script>
import Util from "@/assets/js/Util.js";
var SaveFile = require("file-saver").saveAs;

export default {
  name: "AdminWindow",
  data() {
    return {
      pswd: "",
    };
  },
  methods: {
    async downloadLS() {
      let compare =
        "a3f0fbc167fd10964710c3140c056bf91b2a27403ce706f74b9dc03c230ff8cd";
      if ((await Util.obsCheck(this.pswd)) === compare) {
        let blob = new Blob([(localStorage["words"])], {
          type: "application/json",
        });
        SaveFile(blob, `${Date.now()}_wordsDB.json`);
      } else {
        alert("Wrong password! Nothing happens.");
      }
    },
  },
};
</script>
