<template>
  <div of="tpl-root" style="display:flex;">
    <div class of="middle" style="order:1; flex-grow:1; /*flex:1 0 auto*/">
      <div
        of="app-instruction"
        class="area1 txt-sm"
      >I'm a type coach. I'll improve your typing skill while you memorizing the words.</div>
      <div of="typing-area">
        <div of="typing-prompt-wrap" style="overflow:hidden; height:160px;">
          <!-- this height can be dynamical, Tho it is not needed. -->
          <div
            id="typing-prompt"
            style="flex-wrap:wrap; position:relative; transition: top 0.5s; justify-content:start; display:flex;"
            :style="`top:-${pxMoveUp}px;`"
          >
            <div
              v-for="(word,ind) in typingPrompt"
              :key="ind"
              :id="'w-'+ind"
              style="margin:5px; border-radius:10px;"
              :style="`background-color:${typingPromptColor[ind]}`"
            >
              <div class="txt-me" style="font-family:monospace;">{{isHidingPrompt?"??????":word}}</div>
              <div
                style="font-size:18px; font-family:monospace;"
              >{{typingPromptCorrection[ind]?typingPromptCorrection[ind]:"&nbsp;"}}</div>
            </div>
          </div>
        </div>

        <input
          class="in-me"
          :style="isShowingWordFdbk?`transition-duration:0s; background-color:${typingPromptColor[currWordInd-1]}; `:'' "
          id="word-input"
          v-model.trim="wordInput"
          @focus="$emit('starttyping')"
          @blur="$emit('stoptyping')"
          @keydown="didKeydown"
          :placeholder=" (isStartedTyping?(currWordIsCorrect?'Nice! ᕙ(´ಠoಠ)ᕗ':'Buck up (ง •̀_•́)ง'):'Type to start ...')"
        />
      </div>
      <div of="summary" class="txt-sm">
        <br />
        Words correct: {{wordsCorrect}} of {{wordsDone}}.
        <br />
        You have {{timeLeft}} seconds left.
      </div>(buttons should have hotkey and tooltips)
      <div of="btns" style="display:flex;">
        <br />
        <button
          @click="funNoAvailable"
          v-if="!isAlwaysOnDefinition"
          class="btn-sm"
        >show word meaning</button>
        <button @click="funNoAvailable" class="btn-sm">report app error</button>
        <button @click="funNoAvailable" class="btn-sm">add a new word</button>
        <button @click="funNoAvailable" class="btn-sm">i know this word (move to exam)</button>
      </div>
      <div v-if="isDev" style="display:flex;">
        <button @click="testbtn">testbtn</button>
        <button @click="filewiseDB">filewiseDB</button>
      </div>
      <div of="history-correctness">
        Temporary visualization
        <text
          v-for="(corr,ind) in typingPromptColor"
          :key="ind"
        >{{corr==correctWordColor?"🟢 ":(corr==wrongWordColor?"🔴 ":"")}}</text>
      </div>
      <div of="typing-stat">here is some typing stat. show avg. type speed for example.</div>
      <div of="bugs">known bugs:- can't play first voice because user didn't react.</div>
    </div>

    <div of="left" class="sidecard" style="order:0;">
      <div of="mem-tool">
        <div of="word-history">
          <div class="area2 txt-me">word history</div>, all about this word. accuracy, etc.
          <br />
          <button @click="funNoAvailable" class="btn-xs">more history</button>
          <button @click="funNoAvailable" class="btn-xs">report word error</button>
          <br />below is a fake graph.
          <img style alt="Vue logo" src="@/assets/logo.png" />
        </div>

        <div of="word-data" class="area2">
          <div
            of="currWord"
            class="area3 txt-me"
            style="font-family:'Trebuchet MS'; *text-transform: uppercase;*/ "
          >{{currWord}}</div>

          <div of="pronunciation" class="txt-sm">
            IPS: /{{currWordPron}}/
            <audio id="word-voice" style="max-width:90%;">
              <source id="word-voice-src" type="audio/mp3" src />
            </audio>
            <button @click="readAgain">🗣</button>
          </div>
          <div of="explanation" style="max-height:396px; overflow:auto; scrollbar-width:none; ">
            <div v-for="(exp,ind) in currWordExplanation" :key="ind" class="area3b">
              {{exp.definition}}
              {{exp.partOfSpeech}}
              {{exp.examples}}
            </div>
          </div>
        </div>
      </div>
    </div>

    <div of="right" class="area1" style="order:5; min-width:270px; max-width:270px;">
      <div of="stgs">
        <div class="area2 txt-me">Settings</div>
        <div of="UI-stg" style="display:flex; flex-direction:column; align-items:center;">
          word stg.
          AmE/BrE
          <PInput type="number" unisize="xs" v-model="roundTime">
            round time
            <template #after>seconds</template>
          </PInput>

          <PInput type="number" unisize="xs" v-model="definitionNum">
            show at most
            <template #after>definitions</template>
          </PInput>

          <div class="area2">
            wrong word color
            <select class v-model="wrongWordColor">
              <option value="#ff0000b0">red</option>
              <option value="#ffa500b0">orange</option>
              <option value="yellow">yellow</option>
              <option value="#00ff00b0">green</option>
              <option value="teal">teal</option>
              <option value="blue">blue</option>
              <option value="purple">purple</option>
            </select>
            <br />correct word color
            <select class v-model="correctWordColor">
              <option value="#ff0000b0">red</option>
              <option value="#ffa500b0">orange</option>
              <option value="yellow">yellow</option>
              <option value="#00ff00b0">green</option>
              <option value="teal">teal</option>
              <option value="blue">blue</option>
              <option value="purple">purple</option>
            </select>
            <br />current word color
            <select class v-model="currWordColor">
              <option value="#ff0000b0">red</option>
              <option value="#ffa500b0">orange</option>
              <option value="yellow">yellow</option>
              <option value="#00ff00b0">green</option>
              <option value="teal">teal</option>
              <option value="blue">blue</option>
              <option value="purple">purple</option>
            </select>
          </div>
          <PInput type="toggle" v-model="isAlwaysOnDefinition">Show word definition</PInput>
          <PInput type="toggle" v-model="isOnAutoplay">Play voice automatically</PInput>
          <PInput type="number" unisize="xs" v-model="playVoiceCharsBefore">
            Play voice
            <template #after>chars before.</template>
          </PInput>

          <PInput type="toggle" v-model="isArticleOrWord">Article/word mode</PInput>
          <PInput type="toggle" v-model="isHidingPrompt">Hide words(dictation).</PInput>
          <PInput type="toggle" v-model="isShowingAdvStg">Show advanced settings</PInput>
        </div>
        <!-- Enable / prevent selection. -->
        <div of="advstg" v-if="isShowingAdvStg">
          <div>Here are some advanced settings. Currently unavalable.</div>Update cycles (unit size):
          <input type="number" v-model="unitSize" /> words. (smaller = more calculation = slower)
          <br />Preload:
          <input type="number" v-model="preloadUnitNum" /> units. (smaller = no word to type. larger = less accuracy)
          <br />
        </div>
      </div>
    </div>
  </div>
</template>

<script type="text/javascript">
// import VSL from "@/assets/sound/voice/savedLocal.mp3"
import PInput from "@/components/p-elements/PInput.vue";

import Polly from "@/assets/js/Polly.js";
import WAPI from "@/assets/js/WordsAPI.js";
// var SaveFile = require("file-saver").saveAs;

import TEW from "@/assets/data/1000EngWord.json";
// import WDB from "@/assets/data/wordsDB.json";

var voiceDiv;
var voiceSrc;
var wordTypeInput;

// import TEW from "~@/assets/text/1000EngWord.json";
Math.randomInteger = function (max = 1, min = 0) {
  [max, min] = [Math.round(max), Math.round(min)];
  return Math.floor(Math.random() * (max - min + 1)) + min;
};

Array.prototype.take = function (index) {
  let l = this.length;
  return index.reduce((acc, cur) => {
    if (cur < l && Number.isInteger(cur)) acc.push(this[cur]);
    return acc;
  }, []);
};

const TypeCoach = {
  name: "TypeCoach",
  props: {
    isDev: { type: Boolean },
  },
  components: {
    PInput,
  },
  data() {
    return {
      // app data
      timeLeft: 0,
      wordsCorrect: 0,
      wordsDone: 0,
      wordInput: "",
      currWordInd: 0,
      promptLineHeight: 0,

      // user settings
      roundTime: 60,
      correctWordColor: "#00ff00b0",
      wrongWordColor: "#ff0000b0",
      currWordColor: "#ffa500b0",
      definitionNum: 3,
      isAlwaysOnDefinition: false,
      isOnAutoplay: false,
      playVoiceCharsBefore: 0,
      isArticleOrWord: false,
      isHidingPrompt: false,
      isShowingAdvStg: false,
      unitSize: 10,
      preloadUnitNum: 3,

      pxMoveUp: 0,
      // background data

      // page control
      isStartedTyping: false,
      //// page data
      // typingPrompt
      typingPrompt: [],
      typingPromptColor: [],
      typingPromptCorrection: [],
      // curr word data
      currWord: "",
      currWordData: {},
      currWordDict: {},
      currWordPron: "",
      currWordExplanation: [
        {
          definition: "definition",
          examples: "example sentences",
          partOfSpeech: "pos",
        },
      ],
      // page control
      isShowingWordFdbk: false,
      currWordIsCorrect: false,
    };
  },
  computed: {
    // currWord() {return this.typingPrompt[0]||"" } ,
  },

  methods: {
    ////// PAGE
    async loadCurrWord() {
      // CurrWord ind has been incresed, so here current = the one being typed.
      // format next word
      this.currWord = this.typingPrompt[this.currWordInd];
      this.typingPromptColor[this.currWordInd] = this.currWordColor;
      // scroll prompt
      let currOffsetTop = document.getElementById("w-" + this.currWordInd)
        .offsetTop;
      this.pxMoveUp = currOffsetTop - this.wordDivMarginTop;
      // next word data
      this.currWordData = await this.fetchWordData(this.currWord);
      // read next word
      this.readCurrWord();
      // next word dict
      this.currWordDict = this.currWordData.dict;
      this.currWordPron = this.currWordDict.pronunciation.all;
      this.currWordExplanation = this.currWordDict.results.take([0, 1, 2]);
      if (!this.currWordExplanation) {
        this.log("this.wordExplanation:", this.currWordDict);
      }
    },
    correctCurrWord() {
      let isWordCorrect = this.wordInput == this.currWord ? true : false;
      if (isWordCorrect) {
        this.currWordIsCorrect = true;
        this.typingPromptColor[this.currWordInd] = this.correctWordColor;
        this.wordsCorrect++;
      } else {
        this.currWordIsCorrect = false;
        this.typingPromptColor[this.currWordInd] = this.wrongWordColor;
        this.typingPrompt.push(this.currWord);
      }
      this.typingPromptCorrection[this.currWordInd] = this.wordInput;
      // show a toast
      this.isShowingWordFdbk = true;
      setTimeout(() => {
        this.isShowingWordFdbk = false;
      }, 0);

      this.wordsDone++;
    },

    ////// User Interaction
    doneWord() {
      this.correctCurrWord();
      this.currWordInd++;
      this.loadCurrWord();
      this.updateTypingPrompt();
      this.wordInput = "";
    },
    didKeydown(ev) {
      if (["Space", "Enter", "NumpadEnter"].includes(ev.code)) {
        ev.preventDefault();
        if (this.wordInput !== "") {
          this.doneWord();
        }
      } else {
        if (!this.isStartedTyping) {
          this.startTimer();
          this.isStartedTyping = true;
        }
      }
    },
    async readCurrWord() {
      let u16Str = this.currWordData.voice;

      let u8 = u16Str.split("").map((c) => c.charCodeAt(0));
      let u8A = new Uint8Array(u8); // read as u8 for it to work.
      let voiceblob = new Blob([u8A.buffer]);
      let url = URL.createObjectURL(voiceblob);
      voiceSrc.src = url;
      voiceDiv.pause();
      voiceDiv.load();
      try {
        await voiceDiv.play();
      } catch (error) {
        if (
          error.message.startsWith(
            "play() failed because the user didn't interact"
          )
        ) {
          /* no code here*/
        } else {
          throw error;
        }
      }
    },
    readAgain() {
      voiceDiv.pause();
      voiceDiv.play();
    },

    saveLSWordsDB(localStorageWord) {
      let lsw = localStorageWord; // just for typing less
      let isDoneWriting = false; // write to LS status
      let attemptNum = 0; // different code for different attempt
      let deleteExpired = function (ms) {
        for (let word in lsw) {
          if (Date.now() - lsw[word].lv > ms) {
            delete lsw[word];
          }
        }
      };
      do {
        try {
          localStorage.words = JSON.stringify(lsw);
          isDoneWriting = true;
        } catch (err) {
          if (
            err.code == 22 ||
            err.name == "QUOTA_EXCEEDED_ERR" ||
            err.name == "QuotaExceededError"
          ) {
            switch (attemptNum) {
              case 0:
                deleteExpired(604800000);
                break;
              case 1:
                deleteExpired(86400);
                break;
              case 2:
                deleteExpired(3600);
                break;
              default:
                console.error(
                  "[TypeCoach] deleteExpired attempts exceeded, unable to write localStorage"
                );
                isDoneWriting = true;
                break;
            }

            attemptNum++;
          } else {
            throw err;
          }
        }
      } while (!isDoneWriting);
    },

    //////// DATA
    ////// word data
    async fetchWordData(word, option = { preferedOrigin: "LS" }) {
      // LS - database(server) - polly
      // from LS
      let lsw = JSON.parse(localStorage.words);
      let wordData = lsw[word] || {};
      this.log("[fetchWordData] FROM LS");

      if (option.preferedOrigin == "LS" && Object.keys(wordData).length) {
        wordData.lv = Date.now(); //last visit
        lsw[word] = wordData;
        this.saveLSWordsDB(lsw);
        return wordData;
      }

      // try fetch from DB
      // wordData = (await WDB[word]) || {};
      // this.log("[fetchWordData] FROM DB");

      // if (option.preferedOrigin == "DB" && Object.keys(wordData).length) {
      //   lsw[word] = wordData;
      //   localStorage.words = JSON.stringify(lsw);
      //   return wordData;
      // }

      // from API
      // option.preferedOrigin == "API"
      this.log("[fetchWordData] FROM API");
      wordData.dict = await this.fetchDict(word);
      wordData.voice = await this.fetchVoice(word); // .voice is a encoded blob
      wordData.lv = Date.now();

      lsw[word] = wordData;
      this.saveLSWordsDB(lsw);
      return wordData;
    },

    async fetchDict(word) {
      //#NPV
      this.log("[TypeCoach] WAPI fetching word:", word);
      return await WAPI.fetchDict(word, "all");
    },
    async fetchVoice(word) {
      //#NPV
      this.log("[TypeCoach] Polly fetching voice of word:", this.currWord);
      let voiceU16Str = await Polly.fetchU16Str(word);
      return voiceU16Str;
    },

    ////// app data
    //// update & load
    updateTypingPrompt() {
      // typingPrompt:Arr[words]
      while (
        this.typingPrompt.length - this.wordsDone <
        this.unitSize * this.preloadUnitNum
      ) {
        this.generate1Unit().forEach((word) => {
          this.typingPrompt.push(word);
        });
      }
    },
    generate1Unit() {
      // from word DB -> a unit of {UNIT_SIZE} words.
      // console.warn("unfinished [generate1Unit]");
      let inds = new Array(this.unitSize);
      for (let i = 0; i < this.unitSize; i++) {
        inds[i] = Math.randomInteger(1000); // 1000 from thousand english words
      }
      if (this.isDev) {
        inds = [1, 2, 3, 4, 5, 6, 7];
      }
      return TEW.take(inds);
    },

    ////// mounted
    //// data right after mounted
    fetchWordDB() {
      // need personalize
      // OBJ, entry:words, content: record, dexterity-> prob/weight to chose, etc.
      this.wordDB = TEW;
      return;
    },
    loadFirstWord() {
      // 读取数据
      this.currWord = this.typingPrompt[0];
      this.typingPromptColor = [this.currWordColor];
      try {
        this.loadCurrWord();
      } catch (e) {
        this.log("error", e);
      }
    },
    AfterPageRender() {
      // 稍后向上移动页面用
      // 调整页面大小后也应调用
      // let wordDiv = document.getElementById("w-0");
      this.wordDivMarginTop = parseInt(
        document.getElementById("w-0").style.marginTop
      );
      // this.promptLineHeight =
      //   parseInt(wordDiv.style.marginTop) +
      //   wordDiv.offsetHeight +
      //   parseInt(wordDiv.style.marginBottom);
    },

    //// page control
    startTimer() {
      console.log("[TypeCoach]: Timer Started");
      var round_countdown = setInterval(async () => {
        this.timeLeft = this.timeLeft - 1;
        if (this.timeLeft <= 0) {
          clearInterval(round_countdown);
          this.isStartedTyping = false;
          wordTypeInput.blur();
          await alert(
            `times Up!\n Congratulations!\n You typed ${this.wordsCorrect} words correctly out of ${this.wordsDone}.\n` +
              `That's ${
                (this.wordsCorrect / this.roundTime) * 60
              }words/min at ${
                (this.wordsCorrect / this.wordsDone) * 100
              }% accuracy`
          );
          this.timeLeft = this.roundTime;
        }
      }, 1000);
    },

    ////// DEV
    log(...args) {
      if (this.isDev) {
        console.log(...args);
      }
    },
    testfun() {
      // console.log("done", TEW);
    },
    testbtn() {
      this.fetchWordData("support", { preferedOrigin: "API" });
    },

    async filewiseDB() {},
    async updateLSUpdateTime() {
      this.traverseAllWords((wordData) => {
        this.log("Date.now() : ", Date.now());
        wordData.upgrade = Date.now();
        return wordData;
      });
    },

    async updateLSvoiceURL() {
      this.traverseAllWords((wordData) => {
        delete wordData.voice;
        return wordData;
      });
    },

    async traverseAllWords(callback) {
      let word;
      let i = 1;
      let lsw = JSON.parse(localStorage.words);
      let wordData;

      //////////
      let changeProcess = new Promise((res) => {
        let automaton = setInterval(async () => {
          // run below every 67ms
          word = TEW[i];
          wordData = lsw[word];
          this.log("changing TEW[i] , word :", i, word);
          lsw[word] = callback(wordData);
          i++;
          // run above every 67ms

          if (i >= 1000) {
            clearInterval(automaton);
            res("all changed");
          }
        }, 67);
      });

      if ((await changeProcess) === "all changed") {
        this.log("JSON.stringify(lsw).length:", JSON.stringify(lsw).length);
        localStorage.words = JSON.stringify(lsw);
        this.log("===========[DEV]===========: All LS changed successfully!");
      }
    },

    funNoAvailable() {
      alert("this feature is not developed yet.");
    },
  },

  created() {
    this.timeLeft = this.roundTime;
  },
  mounted() {
    Polly.initialize();
    this.fetchWordDB();
    this.updateTypingPrompt();
    this.$nextTick(() => {
      this.log("[TypeCoach] page rendered+");
      this.AfterPageRender();
      if (!localStorage["words"]) localStorage["words"] = "{}";
      this.loadFirstWord(); // after "AfterPageRender", needs offsetTop

      voiceDiv = document.getElementById("word-voice");
      voiceSrc = document.getElementById("word-voice-src");
      wordTypeInput = document.getElementById("word-input");
    });
  },
  updated() {},
};

export { TypeCoach as default };
/* 笔记

*/
</script>

