        "method": "GET",
        "headers": {
            // predetemined below
            "x-rapidapi-host": "wordsapiv1.p.rapidapi.com",
            "x-rapidapi-key": "189294a368mshceb82f6266f8782p1e6297jsn26c49b2c3aa0"
        }
