<template>
  <div id="app" :style="CSSProps">
    <GlobalHeader @admin-login="this.isShowingAdminWindow=true" @change-theme="changeTheme" />
    <div v-show="isDev" class="devdiv">
      <h1>this you are in dev mode.</h1>need a global control panel and router.
    </div>
    <div v-show="!isOnDesktop" class="page-mask">
      Type Like A _____.
      <div
        style="font: 24px sans-serif;"
      >Seems that you are currently using a device with a small screen and without keyboard(hover).</div>We will be waiting for you back with a keyboard.
    </div>

    <TypeCoach
      style="overflow: auto;"
      v-show="isOnDesktop"
      @starttyping="isTyping=true"
      @stoptyping="isTyping=false"
      :isDev="isDev"
    />
    <AdminWindow v-if="isShowingAdminWindow" @close-admin-window="this.isShowingAdminWindow=false" />
    <GlobalFooter ver="verNum" />
  </div>
</template>

<script>
// import HelloWorld from "@/components/HelloWorld.vue";
import GlobalFooter from "@/components/GlobalFooter.vue";
import GlobalHeader from "@/components/GlobalHeader.vue";
import TypeCoach from "@/components/TypeCoach.vue";
import AdminWindow from "@/components/AdminWindow.vue";

const THEME = {
  light: {
    "--ptext": "#111",
    "--pgray0": "#eee",
    "--pgray1": "#ccc",
    "--pgray2": "#aaa",
    "--pgray3": "#999",
  },
  dark: {
    "--ptext": "#eee",
    "--pgray0": "#222",
    "--pgray1": "#444",
    "--pgray2": "#666",
    "--pgray3": "#888",
  },
};

const theApp = {
  name: "App",
  components: {
    GlobalFooter,
    GlobalHeader,
    TypeCoach,
    AdminWindow,
    // HelloWorld,
  },
  data() {
    return {
      // device info
      isOnDesktop: true,
      // global app status
      isDev: false, // true, //,
      verNum: 3,
      currTheme: "dark", //light
      CSSProps: {},
      //
      isTyping: false,

      // windows
      isShowingAdminWindow: false,
    };
  },

  methods: {
    onkeydown(ev) {
      if (this.isTyping) {
        return;
      }
      switch (true) {
        case ev.ctrlKey && ["KeyC", "KeyV", "KeyW"].includes(ev.code):
          console.log("Ctrl CVW");
          break;
        case ev.metaKey && ["KeyC", "KeyV", "KeyW", "KeyR"].includes(ev.code):
          console.log("Meta CVWR");
          break;
        case ["F12", "Space"].includes(ev.code):
          // console.log("F12");
          break;
        default:
          /****************** this is for test  
          ev.preventDefault();
          console.warn("prevented key:", ev.code);
          */
          break;
      }
      this.$emit("kbev");
      // console.log(ev);
      // console.log(ev.code, "is pressed");
    },
    // adminLogin() {
    //   this.isShowingAdminWindow = true;
    // },

    changeTheme() {
      if (this.currTheme == "dark") {
        this.loadTheme("light");
        return;
      }
      if (this.currTheme == "light") {
        this.loadTheme("dark");
        return;
      }
    },
    // render
    getMediaQuery() {
      let mql = window.matchMedia(
        "screen and (hover: hover) and (min-width: 1024px)"
      ); // media query list
      this.isOnDesktop = mql.matches;
    },
    loadTheme(themeName) {
      // document.documentElement.style.setProperty("--testvar", "'preserved'");
      // NOT WORKING document.documentElement.style=THEME[themeName]
      this.currTheme = themeName;
      this.CSSProps = THEME[themeName];
      document.body.style.setProperty("--pgray0", THEME[themeName]["--pgray0"]);
      console.log(" : THEME loaded", themeName);
    },
  },
  created() {
    this.getMediaQuery();
  },
  mounted() {
    document.onkeydown = this.onkeydown;
    this.loadTheme(this.currTheme);
    // this.$broadcast('[BC] keydown')
  },
};
export {theApp as default}
</script>

